/**
 * 
 * @param {Arguments} nums - 
 * @return {Number}
 * @private
 */
function _mult (...nums) {

  let ret = 1

  for (let num of nums) {
    ret *= num
  }

  return ret

}

/**
 * 
 * @param {Arguments} nums - 
 * @return {Number}
 * @private
 */
function _plus (...nums) {

  let ret = 0

  for (let num of nums) {
    ret += num
  }

  return ret

}

/**
 * 
 * // === :  === //
 * // === 1 : === //
 * // === 1.1  === //
 * // === 1.2  === //
 * @param {Function} fn - 
 * @private
 */
function _createProxyFactory (fn) {

  let cache = {}

  return function (...args) {

    const _args = args
      .replace(/[+*]/, '')
      .split(' ')

    if (args in cache) {
      return cache[args]
    }

    return cache[args] = fn.apply(this, _args)

  }

}

/**
 * 
 * @return {Boolean}
 */
export default function () {

  const key = this.state.key

  if (/^[+\d]+$/.test(key)) {

    const proxyPlus = _createProxyFactory(_plus)

    this.props.update_popup({
      isOpen: 1,
      header: ':',
      content: proxyPlus(key)
    })

    return true

  }

  if (/^[+\d]+$/.test(key)) {

    const proxyMult = _createProxyFactory(_mult)

    this.props.update_popup({
      isOpen: 1,
      header: ':',
      content: proxyMult(key)
    })

    return true

  }

  return false

}
