/**
 * cookie
 * // === :    === //
 * // === 1 : "supportWebp=true; csrf_token=1530792624239" === //
 * // === 2  csrf_token= ,  ;   === //
 * @param {String} name - cookie
 * @return {String}
 */
function get (name) {

  const cookie = document.cookie
  const cName = encodeURIComponent(name) + '='
  const cStart = cookie.indexOf(cName)
  let cVal = ''

  if (cStart > -1) {

    let cEnd = cookie.indexOf(';', cStart)
    if (cEnd === -1) {
      cEnd = cookie.length
    }

    cVal = decodeURIComponent(cookie.substring(cStart + cName.length, cEnd))
  }

  return cVal
}

/**
 * cookie
 * @param {String} name - cookie
 * @param {String} value - 
 * @param {[Object]} options - 
 */
function set (name, value, options) {

  if (typeof options !== 'object') {
    throw new Error('options must be a object')
  }

  let cookie = `${name}=${value};`

  if (options.expires) {
    cookie += `; expires=${options.expires.toUTCString()}`
  }
  if (options.maxAge) {
    cookie += `; max-age=${options.maxAge}`
  }
  if (options.domain) {
    cookie += `; domain=${options.domain}`
  }
  if (options.path) {
    cookie += `; path=${options.path}`
  }
  if (options.httpOnly) {
    cookie += `; httponly`
  }
  if (options.sameSite) {
    cookie += `; samesite`
  }
  if (options.secure) {
    cookie += `; secure`
  }

  document.cookie = cookie
}

// === es6 -  - : , , , ;  === //
/*

// 
function m1({x = 0, y = 0} = {}) {
  return [x, y];
}

// 
function m2({x, y} = { x: 0, y: 0 }) {
  return [x, y];
}

// 
m1() // [0, 0]  //  {} , , 
m2() // [0, 0]

// x  y 
m1({x: 3, y: 8}) // [3, 8]
m2({x: 3, y: 8}) // [3, 8]

// x y 
m1({x: 3}) // [3, 0]
m2({x: 3}) // [3, undefined]

// x  y 
m1({}) // [0, 0];
m2({}) // [undefined, undefined]

m1({z: 3}) // [0, 0]
m2({z: 3}) // [undefined, undefined]

*/

/**
 * cookie
 * // === cookie: cookie,  === //
 * @param {String} name - cookie
 * @param {String} value - 
 * @param {[Object]} options - 
 */
function unset (name, options) {
  set(name, '', Object.assign({}, options, {expires: new Date(0), path: '/'}))
}

export default {
  get,
  set,
  unset
}
