/**
 * 
 * // ===  === //
 * // ===  === //
 * @param {String} key - 
 * @return {Boolean}
 */
export default function (key) {

  if (key !== key.split('').reverse().join('')) {
    return false
  }

  this.props.update_popup({
    isOpen: 1,
    header: 'Warning:',
    content: '~~'
  })

  return true

}
