import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { api } from '../../fetch/axios'
import config from '../../../../config'

import './index.css'

export default class Image extends Component {
  constructor (props) {
    super(props)

    this.state = {
      src: this.props.defaultSrc || ''
    }

  }

  static defaultProps = {
    src: ''
  }

  /**
   * loadingGif
   */
  getLoadingGif () {

    api.getIndexStorage()
      .then(res => {

        for (const key in res.data) {
          localStorage.setItem(key, res.data[key])
        }

        this.setState({
          src: localStorage.getItem('loadingGif')
        })

      })
      .catch(err => {
        console.error(err.message)
      })

  }
  
  // === react new Image() bug: === //
  /*
  
    context: undefined,
    onerror:  (e),
    onload:  (),
    props: undefined,
    refs: {},
    src: "https://avatars2.githubusercontent.com/u/20942047?v=4",
    state: {src: ""},
    updater: {isMounted: , enqueueForceUpdate: , enqueueReplaceState: , enqueueSetState: },
    isMounted: (...),
    replaceState: (...)
  
  */
  // === 1 :  react  Image === //
  // === 2 : window Image  document.createElement('img') === //

  /**
   * 
   */
  preload () {

    let img = new window.Image
    img.src = this.props.src

    img.onload = () => {
      this.setState({
        src: img.src
      })
    }
    img.onerror = (e) => {
      console.error(e)
    }
  }

  render () {
    return (
      <img src={this.state.src} className={this.props.className} alt={this.props.alt}/>
    )
  }

  componentDidMount () {

    this.props.src && this.preload()

    //  laoding.gif
    if (this.props.defaultSrc) {
      return
    }
    
    const loadingGif = localStorage.getItem('loadingGif')

    if (loadingGif) {

      this.setState({
        src: loadingGif
      })

    } else {

      this.getLoadingGif()

    }

  }
}

if (config.ISDEV) {
  Image.propTypes = {
    src: PropTypes.string,
    defaultSrc: PropTypes.string,
    alt: PropTypes.string,
    className: PropTypes.string
  }
}
