export default {
  date: /^\d{4}-(?:0[1-9]|1[12])-(?:0[1-9]|[12][1-9]|3[01])(?:\s(?:[01][0-9]|2[0-3])\:[0-6][0-9]\:[0-5][0-9]|$)/,
  phone: /^1(3|5|8)\d{9}$/,
  mail: /^[\w\W]+?@\w+?\.(?:com|cn)$/,
  num: /^\d*$/,
  legal: /[^\*\\\/\?\"\'\+\^]/,

  // 1  \B: \b
  // 2  \B(?=(\d{3}): 3
  // 3  \B(?=(\d{3})(?!\d)): 3
  // 4  + : 
  num2money: /\B(?=(\d{3})+(?!\d))/g,
}
