'use strict'

let fibonacci = {}

/**
 * 
 * // ===  === //
 * // === activetruearr === //
 * // ===  === //
 * @param {Function} fn - 
 * @return {Function}
 */
function tco (fn) {
  let value
  let active = false
  let arr = []

  // 1
  return function () {
    // 2 // 9   // 15
    arr.push(arguments)

    // 3
    if (!active) {
      // 4
      active = true

      // 5  // 11
      while (arr.length) {
        // 6  // 12
        value = fn.apply(this, arr.shift())
      }

      active = false

      return value
    }
  }

  // 10 // 16 ...
}

/**
 * 
 * // === 54, 0, 1; 3, 1, 1;  2, 1, 2;  1, 2, 3 === //
 */
const recurse = tco(function (n, cur = 0, next = 1) {
  // 7  // 13
  if (n === 1) {
    return next
  }

  if (n === 0) {
    return 0
  }

  // 8  // 14
  return recurse(n - 1, next, cur + next)
})

fibonacci.recurse = recurse

/**
 * 
 * // === [0, 1]; i2 === //
 * @param {Number} n - 
 * @return {Array}
 */
fibonacci.iterator = function (n) {
  let arr = [0, 1]
  let len = arr.length
  let i = len

  while (i <= n) {
    arr.push(arr[i - 1] + arr[i - 2])
    ++i
  }

  return arr
}

// ===  === //
// ===        0, n = 0               === //
// === F(n) = 1, n = 1               === //
// ===        F(n - 1) + F(n - 2)    === //
// === A(n) = 1 / 5 * [ ((1 + 5) / 2) ^ n - ((1 - 5) / 2) ^ n ] === //

fibonacci.math = function (n) {
  const SQRT_FIVE = Math.sqrt(5)
  return Math.round(1 / SQRT_FIVE * (Math.pow(0.5 + SQRT_FIVE / 2, n) - Math.pow(0.5 - SQRT_FIVE / 2, n)))
}

export default fibonacci

