export let memorize = fn => {

  let memorize = function (...key) {

    const cache = memorize.cache
    const address = key.join(',')
    
    if (!cache[address]) {
      cache[address] = fn.apply(this, arguments)
    }

    return cache[address]

  }

  memorize.cache = {}
  return memorize

}
