import inheritPrototype from './inheritPrototype'

/**
 * 
 * @constructs Node
 * @param {*} data - 
 * @param {Object} left - 
 * @param {Object} right - 
 */
function Node (data, left, right) {
  this.data = data
  this.left = left
  this.right = right
  this.show = show
}

/**
 * 
 * @return {*}
 */
var show = function () {
  return this.data
}

/**
 * @constructs BST
 * // === null === //
 */
function BST () {
  this.root = null
}

BST.prototype = {

  constructor: BST,

  /**
   * 
   * // ===  === //
   * // === 1. === //
   * // === 2.curcurnull === //
   * @param {*} data - 
   */
  insert: function (data) {
    var n = new Node(data, null, null)

    if (this.root === null) {
      this.root = n
    } else {
      let cur = this.root
      let parent

      while (true) {
        parent = cur

        if (data < cur.data) {
          cur = cur.left

          if (cur === null) {
            parent.left = n
            break
          }
        } else {
          cur = cur.right

          if (cur === null) {
            parent.right = n
            break
          }
        }
      }
    }
  },

  /**
   * 
   * // ===  -  -  === //
   * @param {Object} node - 
   * @param {Function} fn - 
   */
  inOrder: function inner (node, fn) {
    if (!(node === null)) {
      inner(node.left, fn)
      fn(node.show())
      inner(node.right, fn)
    }
  },

  /**
   * 
   * // === nameinner === //
   * // === arguments.calleees5 === //
   * // ===  -  -  === //
   * @param {Object} node - 
   * @param {Function} fn - 
   */
  preOrder: function inner (node, fn) {
    if (!(node === null)) {
      fn(node.show())
      inner(node.left, fn)
      inner(node.right, fn)
    }
  },

  /**
   * 
   * // ===  -  -  === //
   * @param {Object} node - 
   * @param {Function} fn - 
   */
  postOrder: function inner (node, fn) {
    if (!(node === null)) {
      inner(node.left, fn)
      inner(node.right, fn)
      fn(node.show())
    }
  },

  /**
   * 
   * // ===  === //
   * @param {Object} node - 
   * @return {*}
   */
  getMin: function (node) {
    let cur = node || this.root

    while (!(cur.left === null)) {
      cur = cur.left
    }

    return cur.data
  },

  /**
   * 
   * // ===  === //
   * @param {Object} node - 
   * @return {*}
   */
  getMax: function (node) {
    let cur = node || this.root

    while (!(cur.right === null)) {
      cur = cur.right
    }

    return cur.data
  },

  /**
   * 
   * // === null === //
   * @param {*} data - 
   * @return {Null|Object}
   */
  find: function (data) {
    let cur = this.root

    while (cur !== null) {
      if (data === cur.data) {
        return cur
      } else if (data < cur.data) {
        cur = cur.left
      } else {
        cur = cur.right
      }
    }

    return null
  },

  /**
   * 
   * // ===  === //
   * // === 4null, ,   === //
   * // === (null)()()() === //
   * // ===  === //
   * @param {Object} node - 
   * @param {*} data - 
   * @private
   */
  removeNode: function inner (node, data) {
    if (node === null) {
      return null
    }

    if (data === node.data) {
      if (node.left === null && node.right === null) {
        return null
      }

      if (node.left === null) {
        return node.right
      }

      if (node.right === null) {
        return node.left
      }

      let tmpNode = this.getMin(node.right)
      node.data = tmpNode.data
      node.right = inner(node.right, tmpNode.data)

      return node
    } else if (data < node.data) {
      node.left = inner(node.left, data)
      return node
    } else {
      node.right = inner(node.right, data)
      return node
    }
  },

  /**
   * 
   * // === 1 === //
   * @param {*} data - 
   */
  remove: function (data) {
    this.root = this.removeNode(this.root, data)
  }

}

// for count:
function Node4C (data, left, right) {
  Node.apply(this, arguments)
  this.count = 1
}

function BST4C () {
  BST.call(this)
}

inheritPrototype(BST4C, BST)

BST4C.prototype.update = function (data) {
  var d = this.find(data)
  ++d.count
  return d
}

export default BST
