export default function (e) {
  // key
  const key = e.target.value

  // 
  if (key === '') {
    this.setState({
      key,
      selectedData: []
    })
    return
  }

  // selectedData
  let selectedData = []
  this.props.data.forEach(item => {
    let pos = item.name.indexOf(key)
    if ( pos > -1) {
      selectedData.push({
        ...item,
        pos
      })
    }
  })

  // 
  selectedData = selectedData.sort(function (prev, next) {
    return prev - next
  })

  // 
  this.setState({
    key,
    selectedData
  })
}