// === 5.2  === //
/*
html:
< : '&lt;'
> : '&gt;'

html:
& : '&amp;'
" : '&quto;'
' : '&#39;'
*/
// === 5.3 js: `"``\` === //
// === 5.4 :  === //
// === 5.5 CSP === //

/**
 * html
 * // === () === //
 * @param {String} str - html
 * @return {String}
 */
export function escapeHtml (str) {

  str = str + ''

  if (!str) {
    return ''
  }

  return str
    // html
    .replace(/&/g, '&amp;') // 
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    // html property
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#x27;')
}

/**
 * js
 * @param {String} str - js
 * @return {String}
 */
export function escapeJs (str) {

  if (!str) {
    return ''
  }

  return str
    .replace(/\\/g, '\\\\')
    .replace(/"/g, '\\"')

};
