/**
 * 
 * // ===  === //
 * // === 1.10 === //
 * // === 2.10wait === //
 * @param {Function} fn - 
 * @param {Number} wait - 
 * @return {Function}
 */
export default function debounce (fn, wait, immediate) {
  let timer

  return function () {
    let args = arguments
    let self = this

    timer && clearTimeout(timer)

    if (immediate) {
      timer = setTimeout(function () {
        timer = null
      }, wait)

      !timer && fn.apply(self, args)
    } else {
      timer = setTimeout(function () {
        fn.apply(self, args)
      }, wait)
    }
  }
}
